const FormValidation = {
  // 
  validateRequired (title) {
    return { required: true, message: `${title}` }
  },

  // 
  validateSelect (title) {
    return { required: true, message: `${title}` }
  },

  // 
  validatePhone (title) {
    return [
      { required: true, message: `${title}` },
      {
        pattern: /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/,
        message: '11'
      }
    ]
  },

  // 
  validateStringLength (title, min, max) {
    return [
      { required: true, message: `${title}` },
      { min, max, message: `${title} ${min} - ${max} ` }
    ]
  },

  // : 2 => 0 - 99
  validateInteger (title, digit) {
    if (arguments[1]) {
      return [
        { required: true, message: `${title}` },
        {
          pattern: new RegExp(`^[0-9]{1,${digit}}$`),
          message: `${title} 0 - ${generateHighestInteger(digit)} `
        }
      ]
    }

    return [
      { required: true, message: `${title}` },
      { pattern: /^[0-9]+$/, message: `${title}` }
    ]
  }
}

// 9
function generateHighestInteger (digit) {
  let result = ''

  for (let i = 0; i < digit; i++) {
    result += '9'
  }

  return result
}

export default FormValidation
